  background-color: #ccc;
}
.progress-bar{
  height: 6px;
  background-color: #666;
}

/* Mui Switch Code  */

.switch{
  position: relative;
  display: inline-block;
  width: 80px;
  height: 30px;
  text-align: left;
  margin: 20px;
}

.switch input {
  opacity: 0;
  height: 0;
  width: 0;
}

.slider {
  position: absolute;
  cursor: pointer;

  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  background-color: #666;
  transition: 0.4s;
  border-radius: 20px;
  border: 1px solid grey;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 50px;
  left: 1px;
  bottom: 1px;
  background-color: white;
  transition: 0.4s;
  border-radius: 20px;
  /* box-shadow: 1px 1px 1px 1px  #666;  */
}

input:checked + .slider {
  background-color: white;
}

input:checked + .slider:before {
  transform: translate(26px);
  background-color: #666;
}

.today-city {
  border-bottom: 4px solid hsla(0,0%,50.2%,.39);
  padding: 0 5px;
}

.weather-wrapper {
  display: flex;
  justify-content: center;
}

.weather-card, .weather-card-grey {
  margin: 20px 5px;
  border-radius: 20px;
  position: relative;
  overflow: hidden;
  height: 300px;
  width: 300px;
  background-color: #fff;
  box-shadow: 0 0 25px 1px rgba(50 50 50 / 10%);
  -webkit-animation: appear .5s ease-out forwards;
  animation: appear .5s ease-out forwards;
  display: block;
}

.weather-card-grey {
  background-color: hsl(0deg 0% 64%);
  color: #f1f1f1;
}

.sun {
  background-color: #ffcd41;
  border-radius: 50%;
  box-shadow: 0 0 0 4px rgba(255 255 0 / 10%);
  -webkit-animation: light .8s ease-in-out infinite alternate,weather-icon-move 5s ease-in-out infinite;
  animation: light .8s ease-in-out infinite alternate,weather-icon-move 5s ease-in-out infinite;
}

.weather-icon {
  position: relative;
  width: 20%;
  height: 20%;
  top: 0;
  float: right;
  margin: 40px 40px 0 0;
  -webkit-animation: weather-icon-move 5s ease-in-out infinite;
  animation: weather-icon-move 5s ease-in-out infinite;
  box-shadow: 0 0 0 10px rgb(255 255 0 / 20%);
  box-shadow: 0 0 0 17px rgb(255 255 0 / 20%);
  transform: translateY(-8px);
}

.temp, .tempSize  {
  position: absolute;
  bottom: 0;
  left: 25px;
  font-weight: 400;
  font-size: 5rem;
  margin-bottom: 5rem;
 
}

.tempSize {
  font-size: 5rem;
  margin-bottom: 5rem;
  
}

.city, .citySize {
  font-size: 2rem;
  margin-bottom: 2rem;
  position: absolute;
  bottom: 0;
  left: 25px;
  font-weight: 400;
}
 

.btn-fetch:hover {
  background-color: #efefef;
  border: 5px solid #efefef;
}

.btn-grey:hover {
  background-color: white;
  color: black;
}

.btn-fetch, .btn-grey {
  cursor: pointer;
  transition: opacity .5s ease-out;
  position: relative; 
  border: 5px solid #efefef;
  background-color: white;
  padding: .7rem 4rem;
  font-size: 1.3rem;
  /* min-width: 170%; */
  font-weight: 700;
  font-stretch: extra-expanded;
  margin: 10px 0;
  border-radius: 6px;
  display: inline-block;  
  transition: 0.4s;
  left: 50%;
  transform: translate(-50%);
}

.btn-grey {
  background-color: hsl(0deg 0% 64%);
  color: white;
} 


.container {
  display: grid;
  justify-content: center;
}

.card-body, body.dark .card-body {
  transition: all .2s;
  border-radius: 5px;
  height: 180px;
  /* width: 28rem; */
  margin: 20px;
  position: relative;
  /* background-color: #fff;  */

}

.card-body:hover {
  background-color: #efefef;
  box-shadow: 0 2rem 6rem rgb(0 0 0 / 10%);
  transform: translate(-2px);
}

.card-body {
   box-shadow: 0 4rem 6rem rgb(0 0 0 / 10%);
   min-width: 360px;
}

.card-date {
  left: 0;
  bottom: 0;
  padding: 0 0 10px 10px;
  position: absolute;
  font-weight: 700;
  font-size: 1.2rem;
}

.card-weather-emoji {
  font-size: 3rem;
  margin-top: 10px;
  display: inherit;
}

.card-degree {
  bottom: 0;
  padding: 15px;
  right: 0;
  position: absolute;
  font-size: 1.9rem;
  font-weight: 700;
}







